<template>
  <div class="step_four">
    <div class="container">
      <div>
        <h2>Wybrane strony:<br></h2>
        <router-link to="/home/step_one">edytuj</router-link>
        <p v-if="$store.state.Front.boolean">Przód<br><button id="front"
            @click="buttonConf('frontButton')">Potwierdzam</button>
        </p>
        <p v-if="$store.state.Back.boolean">Tył<br><button id="back"
            @click="buttonConf('backButton')">Potwierdzam</button>
        </p>
      </div>
      <div class="picsum">
        <h2>Wybrane picsum:</h2><br>
        <router-link to="/home/step_two">edytuj</router-link>
        <div class="picsum_container">
          <p v-if="$store.state.Front.boolean">Dla przodu:
            <br>
            <img :src="$store.state.Front.url"><br><button id="frontImg"
              @click="buttonConf('frontImgButton')">Potwierdzam</button>
          </p>
          <p v-if="$store.state.Back.boolean">Dla tyłu:
            <br>
            <img :src="$store.state.Back.url"><br><button id="backImg"
              @click="buttonConf('backImgButton')">Potwierdzam</button>
          </p>
        </div>
      </div>
      <div class="effects">
        <h2>Wybrane efekty:</h2><br>
        <router-link to="/home/step_two_addition">edytuj</router-link>
        <p v-if="$store.state.Front.boolean">Dla przodu:</p>
        <div class="effect_container">
        <p v-if="$store.state.Front.boolean">
            <span v-if="$store.state.Front.gray.boolean">Grayscale {{ $store.state.Front.gray.scale }}
              <button id="frontGray" @click="buttonConf('frontGrayButton')">Potwierdzam</button></span>
            <span v-if="$store.state.Front.blur.boolean">Blur {{ $store.state.Front.blur.scale }}
              <button id="frontBlur" @click="buttonConf('frontBlurButton')">Potwierdzam</button></span><br>
        </p>
        </div>
        <p data-v-020eaed6="">Dla tyłu:</p>
        <div class="effect_container">
          <p v-if="$store.state.Back.boolean">
            <span v-if="$store.state.Back.gray.boolean">Grayscale {{ $store.state.Back.gray.scale }}
              <button id="backGray" @click="buttonConf('backGrayButton')">Potwierdzam</button></span>
            <span v-if="$store.state.Back.blur.boolean">Blur {{ $store.state.Back.blur.scale }}
              <button id="backBlur" @click="buttonConf('backBlurButton')">Potwierdzam</button></span><br>
          </p>
        </div>
        <br>
      </div>
      <div class="data">
        <h2>Dane do rachunku:</h2>
        <router-link to="/home/step_three">edytuj</router-link>
        <div v-for="(data, index) in $store.state.PersonalData" :key="index">
        <p>  {{ data }} </p></div><button id="personalData" @click="buttonConf('personalData')">Potwierdzam</button>
      </div>
      <div class="data">
        <h2>Wybrana opcja dostawy:</h2><br>
        <router-link to="/home/step_three_addition">edytuj</router-link><br>
        <p> {{ $store.state.DeliveryChoice }} </p><br><button id="deliveryChoice"
          @click="buttonConf('deliveryChoice')">Potwierdzam</button>
        </div>
      <div v-if="$store.state.DeliveryChoice === 'Wysyłka'">
      <div class="data">
        <h2>Dane do wysyłki:</h2><br>
        <router-link to="/home/step_three_addition">edytuj</router-link>
        <div v-for="(delivery, index) in $store.state.Delivery" :key="index">
        <p>  {{ delivery }} </p></div><button id="deliveryData" @click="buttonConf('deliveryData')">Potwierdzam</button>
      </div>
    </div>
    </div>
    <div class="nav">
      <router-link to="/home/step_three_addition">
        <button>Wstecz</button>
      </router-link>
      <div class="right_nav">
        <button v-if="!this.confirm.personalData || !this.confirm.deliveryChoice || !this.confirm.deliveryData">Potwierdź wybory</button>
        <router-link to="/home/step_five"><button v-if="this.confirm.personalData && this.confirm.deliveryChoice && this.confirm.deliveryData">Złóż zamówienie</button></router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'StepFour',
  data: () => ({
    confirm: {
      front: true,
      back: true,
      frontImg: true,
      backImg: true,
      frontGray: true,
      frontBlur: true,
      backGray: true,
      backBlur: true,
      personalData: true,
      deliveryChoice: true,
      deliveryData: true
    }
  }),
  activated () {
    if (this.$store.state.DeliveryChoice === 'Odbiór Osobisty') {
      this.confirm.deliveryData = true
    } else {
      if (this.confirm.deliveryData) {
        this.confirm.deliveryData = false
        document.getElementById('deliveryData').style.background = 'none'
        document.getElementById('deliveryData').innerText = 'Potwierdzam'
      }
    }
  },
  mounted () {
    this.confirm.personalData = false
    this.confirm.deliveryChoice = false
    if (this.$store.state.DeliveryChoice === 'Wysyłka') {
      this.confirm.deliveryData = false
    }
    for (const [key, value] of Object.entries(this.confirm)) {
      if (`${value}` === 'true' && Boolean(document.getElementById(`${key}`))) {
        document.getElementById(`${key}`).style.background = '#5de794'
        document.getElementById(`${key}`).innerText = 'Potwierdzono!'
      }
    }
  },
  methods: {
    buttonConf (id) {
      document.getElementById(id).style.background = '#5de794'
      document.getElementById(id).innerText = 'Potwierdzono!'
      switch (id) {
        case 'deliveryChoice':
          this.confirm.deliveryChoice = true
          break
        case 'deliveryData':
          this.confirm.deliveryData = true
          break
        case 'personalData':
          this.confirm.personalData = true
          break
      }
    }
  }
}
</script>
<style lang="sass" scpoed>
.step_four
  z-index: 12
  padding: 8px
  .effects
    display: grid
    p
      position: relative
      top: -40%
    .effect_container
      display: flex
      p
        display: flex
        gap: 1em
      span
        display: grid
  .container
    z-index: 13
    display: flex
    gap: 5em
    flex-wrap: wrap
    justify-content: center
    a
      color: #5de794
      margin-bottom: 1rem
      transition: 0.5s
      &:hover
        color: #36475f
    button
      z-index: 13
      border: none
      border-radius: 25px
      padding: 10px
      font-size: 1rem
      font-weight: Bold
      transition: 1s
      cursor: pointer
      &:hover
        background-color: #5de794
        transform: scale(1.05)
    .data
      p
        margin: 0.2em 0em
    .picsum
      display: grid
      .picsum_container
        display: flex
        gap: 2em
    p
      font-size: 1.2em
      margin: 0.5em 0em
    a
      font-size: 1.5em
    h2
      font-size: 2em
      margin-block-end: 0em
    img
      margin: 1em 0em 0em 0em
      width: 7em
      height: 7em
.right_nav
  button
    margin-right: 10px

@media only screen and (max-width: 780px)
.container
  display: grid
</style>
